.card {
    display: flex;
    margin-bottom: 20px;
    margin-left: 20px;
    margin-right: 20px;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
    background-color: skyblue;
    border-radius: 10px;
    box-shadow: 0 1px 2px 1px rgb(0 0 0 / 6%);
  }
  
  .photo {
    width: 90px;
    height: 120px;
    border-radius: 12px;
  }
  
  .description {
    display: flex;
    padding-left: 20px;
    flex-direction: column;
    justify-content: space-around;
  }
  
  #title {
    color: black;
    font-size: 22px;
    font-weight: bold;
    font-family: Verdana;
    margin-top: 10px;
  }
 
  #quotes {
    color: black;
    font-family: Verdana;
    font-weight: 500;
    font-size: 12px;
    margin-top: 5px;
    margin-bottom: 10px;
  }

